.cv-preview {
    width: 50%;
    background: white;
    margin: 3rem auto;
    border-radius: 2px;
    box-shadow: 0 0 20px 2px rgba(0, 0, 0, 0.5);
    display: grid;
    grid-template: 1fr 8fr / 1fr 3fr;
    font-family: 'Barlow', sans-serif;
}

.preview-header {
    background: rgb(26, 73, 117);
    grid-area: 1 / 1 / 2 / 3;
    padding: 2rem 2rem;
    display: flex;
    align-items: flex-end;
    gap: 1rem;
    color: white;
}

.preview-header p {
    font-size: 1.4rem;
}

.preview-header h2 {
    font-size: 2rem;
}

.preview-aside {
    background: #eaebea;
    grid-area: 2 / 1 / 3 / 2;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 3rem 2rem;
}

.personal-details {
    display: flex;
    flex-direction: column;
    gap: 0.3rem;
    font-size: 1.2rem;
    width: 100%;
    align-items: center;
    font-family: 'Roboto', sans-serif;
    word-break: break-all;
    text-align: center;
}

.personal-details h4 {
    margin-top: 0.6rem;
}

.preview-aside h3 {
    margin: 5rem 0 1rem 0;
    font-size: 1.5rem;
}

.skills {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    width: 100%;
}

.skill {
    font-size: 1.2rem;
    word-break: break-all;
    position: relative;
}

.delete-skill {
    cursor: pointer;
    margin-left: 0.4rem;
    position: absolute;
    top: 50%;
    right: -22px;
    transform: translateY(-50%);
}

.skill:hover > .delete-skill {
    opacity: 1;
}

.preview-main {
    grid-area: 2 / 2 / 3 / 3;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.4rem;
    padding: 0.4rem 1.2rem 1.2rem 1.2rem;
}

.preview-main h3 {
    margin: 1rem 0 0.3rem 0;
    font-size: 1.4rem;
}

.description {
    text-align: center;
    font-family: 'Roboto', sans-serif;
}

.educations {
    width: 100%;
    display: flex;
    justify-content: space-around;
    gap: 0.6rem;
}

.educations div {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 0.3rem;
    font-family: 'Roboto', sans-serif;
    text-align: center;
}

.educations div:hover > .delete-education {
    opacity: 1;
}

.study-title {
    font-weight: bold;
    font-size: 1.2rem;
}

.school-name {
    color: #333333;
    font-size: 1.1rem;
}

.work-experiences {
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
}

.job {
    position: relative;
    display: flex;
    flex-direction: column;
    gap: 0.6rem;
    font-family: 'Roboto', sans-serif;
}

.job-details {
    font-size: 1.1rem;
    display: flex;
    justify-content: space-between;
}

.job-position {
    font-weight: bold;
}

.job-company {
    color: #333333;
}

.job-duties {
    text-align: center;
}

.education-dates,
.job-dates {
    font-style: italic;
}

.job:hover > .delete-work-experience {
    opacity: 1;
}

.delete-education,
.delete-work-experience {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
}

.delete-education {
    bottom: -20px;
}

.delete-work-experience {
    bottom: -25px;
}

.delete-skill,
.delete-education,
.delete-work-experience {
    cursor: pointer;
    font-size: 1.2rem;
    background: transparent;
    border: none;
    opacity: 0;
}

.delete-skill:hover,
.delete-education:hover,
.delete-work-experience:hover {
    color: crimson;
}

@media (max-width: 1400px) {
    .cv-preview {
        width: 65%;
    }
}

@media (max-width: 1200px) {
    .cv-preview {
        width: 75%;
    }
}

@media (max-width: 1000px) {
    .cv-preview {
        width: 90%;
    }
}